import * as React from 'react';
import * as ReactDOM from 'react-dom';

import DeveloperToolsConsole from './Components/DeveloperToolsConsole';
import DeveloperToolsConsoleActionCreator from './ActionCreators/DeveloperToolsConsoleActionCreator';
import DeveloperToolsConsoleStore from './Stores/DeveloperToolsConsoleStore';

export default class DeveloperTools {
  private static _instance: DeveloperTools;

  public static get instance(): DeveloperTools {
    if (!this._instance) {
      this._instance = new DeveloperTools();
    }

    return this._instance;
  }

  public initialize(): void {
    const developerToolsDiv: HTMLDivElement = document.createElement('div');
    document.body.appendChild(developerToolsDiv);

    ReactDOM.render(React.createElement(DeveloperToolsConsole), developerToolsDiv);
  }

  public toggleDeveloperTools(): void {
    DeveloperToolsConsoleActionCreator.showHideConsole(!DeveloperToolsConsoleStore.visible);
  }

  public showHideDeveloperTools(show: boolean): void {
    DeveloperToolsConsoleActionCreator.showHideConsole(show);
  }
}
